/*  */
@use "global" as *;

html {
  //~375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  //375px~787px
  font-size: 16px;

  @include mq("md") {
    font-size: vw(strip-unit($inner), 16);
  }

  //inner~max-screen
  @media (min-width: $inner) {
    font-size: 16px;
  }
}

/* pc */
a[href^="tel:"] {
  @include mq("md") {
    pointer-events: none;
  }
}

/*  */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
  @include mq("md") {
    &:hover {
      opacity: 0.8;
    }
  }
}
body {
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  top: 0 !important; // Google
}

body.fade-in {
  opacity: 1;
}

// GoogleUI
.goog-te-banner-frame,
.goog-te-balloon-frame {
  display: none !important;
}

#google_translate_element {
  display: none !important;
}

.skiptranslate {
  display: none !important;
}
