@use "global" as *;

.p-header {
  position: fixed;
  left: 0;
  width: 100%;
  z-index: 1000;
  background: transparent;
  top: rem(32);
  @include mq(md) {
    top: rem(55);
  }
}

.p-header__inner {
  max-width: rem(1820);
  width: 100%;
  margin-inline: auto;
  padding-left: rem(20);
  padding-right: rem(20);
  display: flex;
  justify-content: space-between;
  position: relative;
}

.p-header__logo {
}

.p-header__logo-link {
  display: block;
  width: rem(220);
  @include mq(md) {
    width: rem(540);
  }
}
.p-header__logo-link img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}

.p-header__nav {
  display: none;

  @include mq(md) {
    display: flex;
    margin-left: auto;
    flex: 1;
    opacity: 1;
    visibility: visible;
    margin-top: rem(16);
    transition: 0.3s ease-in-out;
  }
}
.p-header__nav.is-active {
  opacity: 0;
  visibility: hidden;
}

.p-header__menu {
  display: flex;
  gap: rem(33);
  margin-left: auto;
}

.p-header__menu-link {
  display: block;
  color: #fafafa;
  font-size: rem(16);
  font-weight: 400;
  letter-spacing: 0.15em;
  font-family: $second-font-family;
  text-decoration: none;
  transition: opacity 0.3s ease;
  line-height: 1.5;
  writing-mode: vertical-rl;
  text-orientation: upright;
}

.p-header__drawer {
  margin-left: rem(33);
}
.p-header__btns {
  position: fixed;
  right: 0;
  bottom: rem(100);
}

.p-header__btn-link {
  display: block;
  width: rem(70);
}
.p-header__btn-link img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}
.p-header__mice {
  position: fixed;
  right: rem(105);
  bottom: rem(120);
}

.p-header__mice-link {
  display: block;
  width: rem(360);
  filter: drop-shadow(5px 5px 10px rgba($color: #000, $alpha: 0.16));
}

.p-header__mice-link img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}
.p-header__mice-cross {
  display: block;
  width: rem(30);
  position: absolute;
  top: rem(-16);
  right: rem(-16);
  z-index: 3;
  cursor: pointer;
}
.p-header__mice-cross img {
  width: 100%;
  height: auto;
  object-fit: cover;
  object-position: center center;
}
